.spinnable {
  font-size: 11px !important;
  height: 16px !important;
  width: 16px !important;
  margin: 0 !important;
  padding: 0 !important;
  line-height: 10px !important;
  vertical-align: 3px;
  display: inline-block;
}
.spin {
  font-size: 11px;
  height: 16px;
  width: 16px;
  margin: 0;
  padding: 0;
  line-height: 10px;
  display: inline-block;
}
.spin svg {
  animation: octicon-spin 2s linear infinite;
}
@keyframes octicon-spin {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spinner-animation {
  0% {
    transform: rotateZ(0deg);
    animation-timing-function: cubic-bezier(0, 0, 0.8, 0.2);
  }
  50% {
    transform: rotateZ(180deg);
    animation-timing-function: cubic-bezier(0.2, 0.8, 1, 1);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.loading-spinner-tiny {
  position: relative;
  display: block;
  width: 1em;
  height: 1em;
  font-size: 12px;
  background: radial-gradient(#073655 0.1em, transparent 0.11em);
}
.loading-spinner-tiny::before,
.loading-spinner-tiny::after {
  content: "";
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: 1em;
  border: 2px solid;
  animation: spinner-animation 1.2s infinite;
  animation-fill-mode: backwards;
}
.loading-spinner-tiny::before {
  border-color: #073655 transparent transparent transparent;
}
.loading-spinner-tiny::after {
  border-color: transparent #0d639c transparent transparent;
  animation-delay: 1.2s / 2;
}
.loading-spinner-tiny.inline-block {
  display: inline-block;
}
.loading-spinner-tiny::before,
.loading-spinner-tiny::after {
  border-width: 1px;
}
